import React from 'react'
import {KnowledgeConsumer} from './KnowledgeContext'

export default (props) => (
  <KnowledgeConsumer>
    {({wisdom}) => (
      <React.Fragment>
        <h2>{wisdom.german}</h2>
        <em>{wisdom.serbian}</em>
      </React.Fragment>
    )}
  </KnowledgeConsumer>
)



