import React from 'react'
import data from './data/sentences.json'

class Generator {
  constructor (data) {
    this.knowledge = data
  }
  run = () => {
    const count = this.knowledge[this.knowledge.length - 1].id
    const index = Math.floor(Math.random() * count)

    return this.knowledge[index]
  } 
}

const KnowledgeContext = React.createContext({wisdom: ''})

export class KnowledgeProvider extends React.Component {
  state = {
    wisdom: {german: 't', serbian: 'r'}
  }
  componentDidMount () {
    this.generator = new Generator(data)
    this.shuffle()
  }
  shuffle = () => this.setState({wisdom: this.generator.run()})
  render () {
    return (
      <KnowledgeContext.Provider
        value={{
          wisdom: this.state.wisdom,
          shuffle: this.shuffle
        }}
      >
        {this.props.children}
      </KnowledgeContext.Provider>
    )
  }
}

const KnowledgeConsumer = KnowledgeContext.Consumer

export {KnowledgeConsumer}



